
import Vue from "vue";
import { AdvisorInsight } from "../../models/advisor";

export default Vue.extend({
  props: {
    insight: AdvisorInsight,
  },
  methods: {
    dismiss(insight: AdvisorInsight) {
      this.$store.commit("advisor/dismissInsight", insight.id);
    },
    openLink(url: string) {
      window.open(url, "_blank");
      return;
    },
  },
});
