
import Vue from "vue";

export default Vue.extend({
  data: function () {
    return {
      password: "",
    };
  },
  computed: {
    wrongPassword() {
      return this.$store.state.accounts.wrongPassword;
    },
  },
  methods: {
    async applyPassphrase() {
      await this.$store.dispatch("accounts/applyPassphrase", this.password);
      const firstEntry = document.querySelector(
        ".entry[tabindex='0']"
      ) as HTMLElement;
      firstEntry?.focus();
    },
  },
});
